<template>
  <div class="topics">
    <img class="loading" src="../../../static/images/loading.gif" v-if="!topics.length" alt="">
    <router-link :to="'/index/contents/'+topic.id" class="titles" v-for="topic in topics" :key='topic.tabs'>
      <!-- 用户头像 -->
      <img v-lazy="topic.author.avatar_url" alt />
      <!-- 回复量和访问量 -->
      <div class="replyCount">
        <span>{{topic.reply_count}}</span>
        <span>/</span>
        <span>{{topic.visit_count}}</span>
      </div>
      <!-- 类别 -->
      <div class="classes">
        <span class="top" v-if="topic.top">置顶</span>
        <span class="good" v-else-if="topic.good">精华</span>
        <span  v-else>{{tabs[topic.tab]}}</span>
      </div>
      <!-- 标题 -->
      <p class="title">{{topic.title}}</p>
      <!-- 最近一次回复时间 -->
      <div class="replyTime">{{topic.last_reply_at | dateFormat}}</div>
    </router-link>
    <div class="goTop" @click="goTop" v-show="isTopShow">
      <span class="iconfont icon-fanhuidingbu"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Topics",
  data() {
    return {
      // 接收数据
      tabs:{
        share:'分享',
        ask:'问答',
        job:'招聘',
        dev:'测试',
      },
        // 表示滚动条的值
      scrollTop:0,
      //控制 回到顶部容器 显示/隐藏
      isTopShow: false
    };
  },
  props:['topics'],
  // 加载模块
  components: {
    // infiniteLoading,
  },

  // 钩子函数
  mounted() {
    window.addEventListener("scroll",this.myScroll)
  },

  // 实例销毁的时候调用
  destroyed() {
    window.removeEventListener("scroll",this.myScroll);
  },

  // 方法
  methods: {
    goTop:function(){
      // console.log(this.scrollTop)
      let step = Math.floor(-this.scrollTop/10);
      // 定时器
      let timer = setInterval(()=>{
        // 把当前 滚动的值 减掉 每次需要移动的 步长
        document.documentElement.scrollTop =window.scrollTop = document.body.scrollTop =window.pageYOffset =  this.scrollTop + step;
        // 判断是否已经回到了顶部
        if(this.scrollTop<1){
          // 回到顶部 释放定时器
          clearInterval(timer)
        }
      },10);
    },

    // 监听滚动条触发的函数
    myScroll:function(){
      this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.scrollTop || window.pageYOffset;
      if(this.scrollTop>200){
        this.isTopShow = true;
      }else{
        this.isTopShow = false;
      }
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
// 引入基础样式
@import "../../assets/sass/base.scss";
// 引入列表样式
@import "../../assets/sass/topic.scss";
.goTop{
  position: fixed;
  bottom: rem(150);
  right:rem(10);
  z-index: 9;
  span{
    font-size: rem(60)
  }
}

</style>



