<template>
  <div class="contents">
    <div class="showTime">
      <div class="topBar">
        <div class="topTitle">{{title}}</div>
        <ul class="titleList">
          <li>·发布于 {{titles.create_at | dateFormat}}</li>
          <li>·作者 {{loginname}}</li>
          <li>·{{titles.visit_count}} 次浏览</li>
          <li>·来自 {{tabs[tab]}}</li>
        </ul>
      </div>
      <div class="content" v-html="content"></div>
    </div>

    <div class="replies">
      <div class="answer">{{replies.length}} 回复</div>
      <div class="comment" v-for="replie in replies" :key="replie.id">
        <img v-lazy="replie.author.avatar_url" alt />
        <div class="right">
          <div class="rightTop">
            <div class="left">
              <span>{{replie.author.loginname}}</span>
              <span></span>
              <span>{{replie.create_at | dateFormat}}</span>
            </div>
            <div class="icon">
              <span class="iconfont icon-dianzan"></span>
              <span>{{replie.ups.length}}</span>
            </div>
          </div>
          <p v-html="replie.content"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入获取数据方法
import { getContent } from "@/components/getDate/topicsList";
export default {
  name: "Contents",
  data() {
    return {
      tabs: {
        share: "分享",
        ask: "问答",
        job: "招聘",
        dev: "测试"
      },
      titles: [],
      replies: [],
      title: "",
      loginname: "",
      content: "",
      tab: ""
    };
  },
  mounted() {
    var id = this.$route.params.id;
    getContent(id).then(data => {
      this.titles = data.data.data;
      this.replies = data.data.data.replies
      this.title = data.data.data.title;
      this.loginname = data.data.data.author.loginname;
      this.content = data.data.data.content;
      this.tab = data.data.data.tab;
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import "../../assets/sass/base.scss";
@import "../../assets/sass/contents.scss";

</style>



