// 引入axios
import axios from 'axios'

// 配置axios
const $http = axios.create({
    baseURL: 'https://cnodejs.org/api/v1/',
    timeout: 5000,
    headers: {'X-Custom-Header': 'foobar'}
  });

// 创建获取数据方法
function getTopic(response){
    var getObject = Object.assign({
        tab:'all',
        page:1,
        limit:10
    },response)
   return $http({
    //    类别名
        url: 'topics',
        method: 'get', // default
        params: getObject
    })
}
function getContent(id){
   return $http.get('topic/'+id)
}
// 暴露获取数据方法
export {getTopic,getContent}


