import Vue from 'vue'
import Router from 'vue-router'
// 引入一级路由
import Index from '@/components/Index'
import Code from '@/components/Code'
import Me from '@/components/Me'
// 引入二级路由
import All from '@/components/topicsList/All'
import Good from '@/components/topicsList/Good'
import Share from '@/components/topicsList/Share'
import Ask from '@/components/topicsList/Ask'
import Job from '@/components/topicsList/Job'
import Dev from '@/components/topicsList/Dev'
import Contents from '@/components/topicsList/Contents'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      // name: 'Index',
      component: Index,
      children:[
        {
          path: '/',
          name: 'All',
          component: All,
        },
        {
          path: 'good',
          name: 'Good',
          component: Good,
        },
        {
          path: 'share',
          name: 'Share',
          component: Share,
        },
        {
          path: 'ask',
          name: 'Ask',
          component: Ask,
        },
        {
          path: 'job',
          name: 'Job',
          component: Job,
        },
        {
          path: 'dev',
          name: 'Dev',
          component: Dev,
        },
        {
          path: 'contents/:id',
          name: 'Contents',
          component: Contents,
        }
      ]
    },
    {
      path: '/code',
      name: 'Code',
      component: Code
    },
    {
      path: '/me',
      name: 'Me',
      component: Me
    },
    {
      path:'/*',
      redirect:'/index'
    }
  ]
})



