<template>
  <div class="dev">
    <topics :topics="topics"></topics>

    <div class="bottom" v-show="isShow">我也是有底线的</div>
    <!-- 引入无限加载模块 -->
    <infinite-loading @infinite="infiniteHandler" v-if="isIconShow"></infinite-loading>
  </div>
</template>

<script>
// 引入获取数据方法
import { getTopic } from "@/components/getDate/topicsList";
// 引入无限加载模块
import infiniteLoading from "vue-infinite-loading";

// 引入topics
import topics from "@/components/topicsList/topics";
export default {
  name: "Dev",
  data() {
    return {
      // 接收数据
      topics: [],
      page: 1,
      isShow: false,
      isIconShow: false,
      // 标题
      title: ""
    };
  },
  // 创建数据请求方法
  methods: {
    infiniteHandler($state) {
      setTimeout(() => {
        //调用请求数据函数,每次请求完让page加1
        getTopic({ tab: "dev", page: ++this.page, limit: 10 }).then(data => {
          //拼接数组
          this.topics = this.topics.concat(data.data.data);
          if (data.data.data.length == 0) {
            this.isShow = true;
            this.isIconShow = false;
          }
        });
        $state.loaded();
      }, 3000);
    }
  },
  mounted() {
    // 调用方法
    getTopic({ tab: "dev", page: this.page, limit: 20 }).then(data => {
      this.topics = data.data.data;
      this.title = data.data.data.title;
      this.isIconShow = true;
    });
  },
  // 加载模块
  components: {
    infiniteLoading,
    topics
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import "../../assets/sass/base.scss";
.dev {
  margin: rem(160) 0 rem(150);
  
}
</style>



