<template>
  <div id="app">
    <div class="topLogo">
      <img src="../static/images/logo.png" alt />
      <span>这是一个疯狂的APP</span>
    </div>
    <ul class="bottomMenu">
      <router-link to='/index' tag="li" :class="routeName=='index'?'active':''">
        <span class="iconfont" :class="routeName=='index'?'icon-homepage_fill':'icon-homepage'"></span>
        <p>首页</p>
      </router-link>
      <router-link to='/code' tag="li" :class="routeName=='code'?'active':''">
        <span class="iconfont" :class="routeName=='code'?'icon-createtask_fill':'icon-createtask'"></span>
        <p>服务中心</p>
      </router-link>
      <router-link to='/me' tag="li" :class="routeName=='me'?'active':''">
        <span class="iconfont" :class="routeName=='me'?'icon-people_fill':'icon-people'"></span>
        <p>关于我们</p>
      </router-link>
    </ul>

    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",

//计算属性
  computed: {
    routeName:function(){
      var arr = this.$route.path.split('/')
      return arr[1]
    }
  },
  

};
</script>

<style lang='scss'>
@import "./assets/sass/base.scss";
// 顶部导航设置
.topLogo {
  width: 100%;
  background: green;
  height: rem(80);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  img {
    display: block;
    width: rem(60);
    height: rem(60);
    margin: 0 rem(20);
  }
  span {
    color: pink;
    font-size: rem(30);
  }
}
// 底部导航设置
.bottomMenu {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #f2f2f2;
  height: rem(100);
  display: flex;
  justify-content: space-around;
  li {
    width: 30%;
    border-radius: rem(10);
    text-align: center;
    color: #333;
    span {
      display: block;
      font-size: rem(50);
    }
    p{
        font-size: rem(20);
    }
  }
}
.active{
  background: pink !important;
  color: red !important;
}
</style>



